import { css } from 'styled-components';

import { tablet, laptop } from '#theme/devices';
import { primaryFont, secondaryFont } from '#theme/fontFamilies';

export const h2 = css`
  font: bold 28px/32px ${primaryFont};

  @media ${tablet} {
    font: bold 30px/34px ${primaryFont};
  }

  @media ${laptop} {
    font: bold 32px/36px ${primaryFont};
  }
`;

export const h3 = css`
  font: bold 24px/28px ${primaryFont};

  @media ${tablet} {
    font: bold 26px/30px ${primaryFont};
  }

  @media ${laptop} {
    font: bold 28px/32px ${primaryFont};
  }
`;

export const h4 = css`
  font: bold 28px/32px ${secondaryFont};

  @media ${tablet} {
    font: bold 30px/34px ${secondaryFont};
  }

  @media ${laptop} {
    font: bold 32px/36px ${secondaryFont};
  }
`;

export const h5 = css`
  font: bold 24px/28px ${secondaryFont};

  @media ${tablet} {
    font: bold 26px/30px ${secondaryFont};
  }

  @media ${laptop} {
    font: bold 28px/32px ${secondaryFont};
  }
`;

export const h6 = css`
  font: bold 16px/20px ${secondaryFont};
  text-transform: uppercase;
`;

export const p = css`
  font: normal 19px/27px ${primaryFont};
`;

export const a = css`
  color: ${({ theme }) => theme.color.actionBrand.base};
  text-decoration: underline;

  &:hover {
    color: ${({ theme }) => theme.color.actionBrand.alt};
  }
`;
